<script>
import carousel from 'vue-owl-carousel'
import axios from "axios";
import ProfileDialog from "@/components/Misc/ProfileDialog";

export default {
  name: "OurTeamSection",
  created() {
    this.fetchTeam();
  },
  components: {ProfileDialog, carousel},
  data () {
    return {
      profileData : {
        idSelected : null,
        dialog : false,
      },
      isActive: false,
      items : []
    }
  },
  methods : {
    toSeeProfile (id) {
      this.profileData.idSelected = id;
      this.profileData.dialog = true;
    },
    fetchTeam(){
      axios.get(this.$store.state.host + '/accounts/api/v1/my-team/', ).then(response => {
        this.items = response.data.data;
      })
    }
  },
};
</script>

