<template>
  <div class="brown lighten-5">
    <v-container>
      <v-row class="flex-wrap flex-md-nowrap layout-pb-xl layout-pt-lg">
        <v-col md="7" cols="12" class="pt-col order-1 order-md-0">
          <div
              class="mb-minus-lg md:mb-0 md:ml-0 ml-minus-col-1 -left-line bg-beige-dark px-64 py-80 sectionHeading sm:px-32 sm:py-48 flex-grow-1 elementor-widget-wrap elementor-element-populated">
            <div
                class="elementor-element elementor-element-ebbda8d elementor-widget elementor-widget-highlighted-heading"
                data-id="ebbda8d" data-element_type="widget" data-widget_type="highlighted-heading.default">
              <div class="elementor-widget-container">
                <span class="highlighted-heading__title sectionHeading__subtitle text-white">درباره ما</span></div>
            </div>
            <div v-html="content" style="color:#fff;" ></div>
          </div>
        </v-col>
        <v-col class="order-0 order-md-1 col2-mr" style="overflow: hidden;" md="5" cols="12">
          <img src="../../assets/image/5.jpg" width="100%"/>

        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "AboutSection",
  created() {
    this.fetchAboutUs();
  },
  data () {
    return {
      content : null,
    }
  },
  methods : {
    fetchAboutUs () {
      this.loading = true;
      axios.get(this.$store.state.host + '/website/api/v1/website/about-us').then(response=>{
        this.content = response.data.data.content;
      }).catch(error=>{
        this.errorHandle(error.response)
      }).finally(()=>{
        this.loading = false;
        this.$store.state.loading = false;
      })
    }
  }
}
</script>

